import React from "react";
import styled from "styled-components";

// Import komponentw
import RenderTitle from "../components/RenderTitle";

const BoxInfo = styled.div`
  width: 90%;
  margin: 1.5rem auto;
  color: #4e5255;

  .green {
    color: #29b35e;
    font-weight: 6500;
  }

  a {
    text-decoration: none;
    color: #29b35e;
  }

  .title {
    font-weight: 600;
    margin-bottom: 0.25rem;
  }
`;

const Faq = () => {
  return (
    <div data-aos="fade-zoom-in">
      <RenderTitle textDark={"FAQ"} textGreen={""} />
      <BoxInfo>
        <p className="title">
          Skd aplikacja <span className="green">Money</span> pobiera
          informacje?
        </p>
        <p>
          Aplikacja pobiera dane o aktualnym kursie walut oraz histori zmiany
          kursu maksymalnie 30 dni wstecz z darmowego, dostpnego dla kadego
          API udostpnianego przez Narodowy Bank Polski (
          <a href="http://api.nbp.pl/">api.nbp.pl</a>).
        </p>
      </BoxInfo>
      <BoxInfo>
        <p className="title">Jak czsto aktualizowane s kursy walut?</p>
        <p>
          Wprost na danie uytkownika. Za kadym ponownym zaadowaniem strony
          kursy s aktualizowane do najwieszych informacji udostpnianych
          przez rdo czyli Narodowy Bank Polski.
        </p>
      </BoxInfo>
      <BoxInfo>
        <p className="title">Gdzie aplikacja zapisuje dane?</p>
        <p>
          Ta strona internetowa nie uywa cookies. Aby zachowa wiksze
          bezpieczestwo przez utrat danych, ktre uytkownik moe zapisa
          (czyli doda kursy do obserwowanych i mie do nich szybki dostp)
          aplikacja uywa Local Storage (pami wewntrzna) przegldarki. Dane
          zachowywane s na urzdzeniu tak dugo a zostan rcznie usunite
          przez uytkownika.{" "}
        </p>
      </BoxInfo>
      <BoxInfo>
        <p className="title">Z jakich technologii korzysta aplikacja?</p>
        <p>
          <span className="green">Money </span> zostaa zaprojektowana za pomoc
          frameworka React, TypeScript'u, biblioteki komponentw{" "}
          <a href="https://mui.com/">MUI</a>,{" "}
          <a href="https://recharts.org/en-US">Recharts</a>,{" "}
          <a href="https://react-select.com/home">React Select</a>, HTML, CSS.{" "}
        </p>
      </BoxInfo>
    </div>
  );
};

export default Faq;
